.container {
  margin-top: 5rem;
  width: 25%; /* width: 25%;  680=1920/4*/
  // outline: 1px solid darkmagenta;
  padding-right: 32px;
  padding-left: 32px;
}

.usr-mb-2 {
  margin-bottom: 2rem;
}

.rmb-24px {
  margin-bottom: 24px;
}

.rmr-9px {
  margin-right: 9px;
}

.rmr-24px {
  margin-right: 24px;
}


.box {
  border-bottom: 1px solid #cdddcd;
  width: 100%;
}

.btn {
  font-size: 1rem;
}

.custom-control-label {
  font-size: 1rem;
  padding-left: 0.5rem;
}


.custom-control-label::before {
  margin-top: -3px;
  // left:-1.25rem;
  border-color: black;
  border-width: 2px;
  width: 1.1rem;
  height: 1.1rem;
}

.custom-control-label::after {
  top: 0.15rem;
  background-size: 80% 80%;
  // left:-0.2rem;
}

.material-icons-color-white {
  color: white;
}
.material-icons-color-red {
  font-size: 1rem;
  color: #e3006e;
  font-weight: 500;
}

.input-group-text {
  background-color: #ffffff;
  border: none;
}

.warningTxt {
  font-size: 1rem;
  color: #e3006e;
}

.btn-info {
  background-color: #ffffff;
  color: #cdddcd;
  border:  1px solid #CDDDCD;
}

.btn-Cancel {
  font-size: 16px;
  font-weight: 500;
  color: #2a8356;
  border-color: #2a8356;
  background-color: #ffffff;
}

.btg{
  height: 35px;
}
.btn-Update {
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
  border-color: #2a8356;
  background-color: #2a8356;
}

.btn-outline-light {
  text-align: left;
}

.btn-lg-alt {
  height: 35px;
  width: 112px;
}

.btn-lg-alt2 {
